import React, { Component } from 'react';
import Envelope from '../envelope/envelope';
import '../shared/filter.css';

class LowPassFilter extends Component {
    constructor(props) {
        super(props);

        this.state = {
          context: props.context,
          output: props.output,
          source: props.source,
          master: props.context.createGain(),

          lfo1Source: props.lfo1,
          lfo1: props.context.createGain(),
          lfo1FreqOn: 0,
          lfo1Intensity: 10,

          lfo2Source: props.lfo2,
          lfo2: props.context.createGain(),
          lfo2FreqOn: 0,
          lfo2Intensity: 10,

          lpFilter: props.context.createBiquadFilter(),
          filterOn: 1,
          lpCutOff: 600,
          lpResonance: 5,

          frequency: props.frequency,
          envModOn: 1,
          env_amount: 2240,
          peak: 9600,
          attack: 2,
          decay: 5,
          sustain: 4,
          release: 3,

          preset: props.preset
        };

        this.envOn = this.envOn.bind(this);
        this.envOff = this.envOff.bind(this);
        this.updateEnvAmount = this.updateEnvAmount.bind(this);
        this.enableDisableEnvMod = this.enableDisableEnvMod.bind(this);
        this.enableDisableLfo1Freq = this.enableDisableLfo1Freq.bind(this);
        this.enableDisableLfo2Freq = this.enableDisableLfo2Freq.bind(this);
        this.changeLfo1Intensity = this.changeLfo1Intensity.bind(this);
        this.changeLfo2Intensity = this.changeLfo2Intensity.bind(this);
        this.enableDisableFilter = this.enableDisableFilter.bind(this);
        this.changeLpFilterCutOff = this.changeLpFilterCutOff.bind(this);
        this.changeLpFilterResonance = this.changeLpFilterResonance.bind(this);
    }

    updateSynthState(){
      let componentState = {};
      for (var key in this.state) {
        if ( typeof(this.state[key]) !== 'object' ) {
          componentState[key] = this.state[key];
        }
      }
      let label = "LowPassFilter";
      window.synfesizer.presetState[label] = componentState;
    }

    componentDidUpdate(){
      this.updateSynthState();
    }

    componentDidMount(){
      this.updateSynthState();
    }

    envOn(){
      let env_amount = this.state.env_amount,
          attack = this.state.attack,
          decay = this.state.decay,
          sustain = Math.round((this.state.sustain / 5) * env_amount ),
          target = this.state.lpFilter.detune,
          latency = 0.001;

      target.cancelScheduledValues(this.state.context.currentTime);

      target.linearRampToValueAtTime(0, this.state.context.currentTime );
      target.linearRampToValueAtTime(env_amount, this.state.context.currentTime + latency + attack);
      target.linearRampToValueAtTime(sustain, this.state.context.currentTime + latency + attack + decay);
    }

    envOff(){
      let release = this.state.release,
          target = this.state.lpFilter.detune;

      target.cancelScheduledValues(this.state.context.currentTime + release);

      target.linearRampToValueAtTime(0, this.state.context.currentTime + release);
    }

    initiate(){
      let source = this.state.source,
          lfo1 = this.state.lfo1,
          lfo2 = this.state.lfo2,
          lpFilter = this.state.lpFilter;

        lfo1.gain.setTargetAtTime(this.state.lfo1Intensity, this.state.context.currentTime, 0);
        lfo2.gain.setTargetAtTime(this.state.lfo2Intensity, this.state.context.currentTime, 0);
        lpFilter.type = 'lowpass';
        lpFilter.frequency.setTargetAtTime( this.state.lpCutOff, this.state.context.currentTime, 0);
        lpFilter.Q.setTargetAtTime( this.state.lpResonance, this.state.context.currentTime, 0);

        if (this.state.filterOn) {
          source.connect(this.state.lpFilter);
          lpFilter.connect(this.state.master);
        } else {
          lpFilter.disconnect();
          source.disconnect();
          source.connect(this.state.master);
        }

        if(this.state.lfo1){
          if(this.state.lfo1FreqOn){
            lfo1.gain.setTargetAtTime(this.state.lfo1Intensity, this.state.context.currentTime, 0);
            lfo1.connect(this.state.lpFilter.detune);
          } else {
            if (this.state.lfo1.context.destination.numberOfOutputs > 0) {
              lfo1.disconnect(this.state.lpFilter.detune);
            }
            lfo1.gain.setTargetAtTime(0, this.state.context.currentTime, 0);
            lpFilter.detune.setTargetAtTime(0, this.state.context.currentTime, 0);
            lpFilter.frequency.setTargetAtTime(this.state.lpCutOff, this.state.context.currentTime, 0);
          }
        }

        if(this.state.lfo2){
          if(this.state.lfo2FreqOn){
            lfo2.gain.setTargetAtTime(this.state.lfo2Intensity, this.state.context.currentTime, 0);
            lfo2.connect(this.state.lpFilter.detune);
          } else {
            if (this.state.lfo2.context.destination.numberOfOutputs > 0) {
              lfo2.disconnect(this.state.lpFilter.detune);
            }
            lfo2.gain.setTargetAtTime(0, this.state.context.currentTime, 0);
            lpFilter.detune.setTargetAtTime(0, this.state.context.currentTime, 0);
            lpFilter.frequency.setTargetAtTime(this.state.lpCutOff, this.state.context.currentTime, 0);
          }
        }
    }

    makeConnections(){
      this.state.lfo1Source.connect(this.state.lfo1);
      this.state.lfo2Source.connect(this.state.lfo2);
      if (this.state.filterOn) {
        this.state.source.connect(this.state.lpFilter);
        this.state.lpFilter.connect(this.state.master);
      } else {
        this.state.source.connect(this.state.master);
      }
      this.state.master.connect(this.state.output);
    }

    componentWillMount(){
        this.makeConnections();
        this.initiate();
    }

    componentWillReceiveProps(props){
      if (this.state.preset !== props.preset) {
        this.setState({preset: props.preset}, function(){
          let settings = this.props.preset.settings["LowPassFilter"];
          this.setState(settings, function(){
            this.initiate();
          }.bind(this));
        }.bind(this));
      }

      if(this.state.envModOn){
        if(!!props.note){
          this.envOn();
        } else {
          this.envOff();
        }
      }
    }

    //EventHandlers
    enableDisableEnvMod(){
      if(this.state.envModOn){
        this.setState({envModOn: 0});
      } else {
        this.setState({envModOn: 1});
      }
    }

    enableDisableLfo1Freq(){
      let lfo1 = this.state.lfo1,
          lpFilter = this.state.lpFilter;

      if(this.state.lfo1FreqOn){
        lfo1.gain.setTargetAtTime( 0, this.state.context.currentTime, 0);
        this.setState({lfo1FreqOn: 0}, function(){
          lfo1.disconnect(this.state.lpFilter.detune);
          lpFilter.detune.setTargetAtTime( 0, this.state.context.currentTime, 0);
          lpFilter.frequency.setTargetAtTime( this.state.lpCutOff, this.state.context.currentTime, 0);
        });
      } else {
        this.setState({lfo1FreqOn: 1}, function(){
          lfo1.gain.setTargetAtTime( this.state.lfo1Intensity, this.state.context.currentTime, 0);
          lfo1.connect(this.state.lpFilter.detune);
        });
      }
    }

    enableDisableLfo2Freq(){
      let lfo2 = this.state.lfo2,
          lpFilter = this.state.lpFilter;

      if(this.state.lfo2FreqOn){
        lfo2.gain.setTargetAtTime( 0, this.state.context.currentTime, 0);
        this.setState({lfo2FreqOn: 0}, function(){
          lfo2.disconnect(this.state.lpFilter.detune);
          lpFilter.detune.setTargetAtTime( 0, this.state.context.currentTime, 0);
          lpFilter.frequency.setTargetAtTime( this.state.lpCutOff, this.state.context.currentTime, 0);
        });
      } else {
        this.setState({lfo2FreqOn: 1}, function(){
          lfo2.gain.setTargetAtTime( this.state.lfo2Intensity, this.state.context.currentTime, 0);
          lfo2.connect(this.state.lpFilter.detune);
        });
      }
    }

    enableDisableFilter(){
      if(this.state.filterOn){
        this.setState({filterOn: 0}, function(){
          this.state.lpFilter.disconnect();
          this.state.source.disconnect();
          this.makeConnections();
        });
      } else {
        this.setState({filterOn: 1}, function(){
          this.state.lpFilter.disconnect();
          this.state.source.disconnect();
          this.makeConnections();
        });
      }
    }

    changeLfo1Intensity(event){
      this.setState({lfo1Intensity: parseInt(event.target.value, 0)}, function(){
        this.initiate();
      });
    }

    changeLfo2Intensity(event){
      this.setState({lfo2Intensity: parseInt(event.target.value, 0)}, function(){
        this.initiate();
      });
    }

    changeLpFilterCutOff(event){
      this.setState({lpCutOff:parseInt(event.target.value, 0)}, function(){
        this.initiate();
      });
    }

    changeLpFilterResonance(event){
      this.setState({lpResonance:parseInt(event.target.value, 0)}, function(){
        this.initiate();
      });
    }

    updateAttack(event){
      this.setState({attack: parseFloat(event.target.value)});
    }

    updateDecay(event){
      this.setState({decay: parseFloat(event.target.value)});
    }

    updateSustain(event){
      this.setState({sustain: parseFloat(event.target.value)});
    }

    updateRelease(event){
      this.setState({release: parseFloat(event.target.value)});
    }

    updateEnvAmount(event){
      this.setState({env_amount: parseFloat(event.target.value)});
    }

    // Render
    render() {
        return (
            <div className="filter-holder">
              <div className="filter">
                <div className="buttons">
                  <h2>LP Filter</h2>
                  <button className={this.state.filterOn ? "on": "off"} onClick={this.enableDisableFilter}>{this.state.filterOn ? "LP Filter On": "LP Filter Off"}</button>
                </div>
                <div className="controls">
                  <label>LP Filter Cutoff ({this.state.lpCutOff}Hz)</label>
                  <input className='lp-cutoff' type='range' min='0' max={this.state.peak} step='1' onChange={this.changeLpFilterCutOff} value={this.state.lpCutOff}/>
                  <label>LP Filter Resonance ({Math.round(this.state.lpResonance/32 * 100)}%)</label>
                  <input className='lp-q' type='range' min='0' max='32' step='0.1' onChange={this.changeLpFilterResonance} value={this.state.lpResonance}/>
                </div>
              </div>
              <div className="envelope-controls">
                <div className="buttons">
                  <h2>ENV</h2>
                  <button className={this.state.envModOn ? "on": "off"} onClick={this.enableDisableEnvMod}>{this.state.envModOn ? "ENV Mod On": "ENV Mod Off"}</button>
                  <Envelope
                    label="lpf_env"
                    width="100"
                    height="100"
                    attack={this.state.attack}
                    decay={this.state.decay}
                    sustain={this.state.sustain}
                    release={this.state.release}
                    peak={this.state.peak}
                    peaks={[this.state.env_amount]}
                  />
                </div>
                <div className="controls">
                  <label>Envelope Amount ({Math.round(this.state.env_amount/this.state.peak * 100)}%)</label>
                  <input className='env_amount' type='range' min='0' max={this.state.peak} step='0.01' onChange={this.updateEnvAmount} value={this.state.env_amount} />
                  <label>Attack ({this.state.attack}ms)</label>
                  <input className='attack' type='range' min='0' max='5.0' step='0.01' onChange={this.updateAttack.bind(this)} value={this.state.attack} />
                  <label>Decay ({this.state.decay}ms)</label>
                  <input className='decay' type='range' min='0' max='5.0' step='0.01' onChange={this.updateDecay.bind(this)} value={this.state.decay} />
                  <label>Sustain({Math.round(this.state.sustain/5 *100)}%)</label>
                  <input className='sustain' type='range' min='0' max='5.0' step='0.01' onChange={this.updateSustain.bind(this)} value={this.state.sustain}/>
                  <label>Release({this.state.release}ms)</label>
                  <input className='release' type='range' min='0' max='12' step='0.01' onChange={this.updateRelease.bind(this)} value={this.state.release}/>
                </div>
              </div>
              <div className="lfomod">

                <h2 title="Modulate this filter(LPF) with one of the LFO. Filter(LPF) must be on.">LPF LFO Mod</h2>
                <div className="lfo-1">
                  <div className="buttons" >
                    <button
                      className={this.state.lfo1FreqOn ? "on": "off"}
                      onClick={this.enableDisableLfo1Freq}
                      title="Modulate filter(LPF) with LFO1. Filter(LPF) must be on."
                    >
                      {this.state.lfo1FreqOn ? "LFO 1 Mod On": "LFO 1 Mod Off"}
                    </button>
                  </div>
                  <div className="controls">
                    <label>LFO 1 Modulation Intensity ({Math.round(this.state.lfo1Intensity/100 * 100)}%)</label>
                    <input className='lfo-intensity' type='range' min='0' max='100' step='0.1' onChange={this.changeLfo1Intensity} value={this.state.lfo1Intensity}/>
                  </div>
                </div>
                <div className="lfo-2">
                  <div className="buttons">
                    <button
                      className={this.state.lfo2FreqOn ? "on": "off"}
                      onClick={this.enableDisableLfo2Freq}
                      title="Modulate filter(LPF) with LFO2. Filter(LPF) must be on."
                    >
                        {this.state.lfo2FreqOn ? "LFO 2 Mod On": "LFO 2 Mod Off"}
                    </button>
                  </div>
                  <div className="controls">
                  <label>LFO 2 Modulation Intensity ({ Math.round(this.state.lfo2Intensity/100 * 100) }%)</label>
                    <input className='lfo-intensity' type='range' min='0' max='100' step='0.1' onChange={this.changeLfo2Intensity} value={this.state.lfo2Intensity}/>
                  </div>
                </div>
              </div>
          </div>
        );
    }
}
export default LowPassFilter;



