import React, { Component } from 'react';
import './keyboard.css';

class Keyboard extends Component {
    constructor(props) {
        super(props);
        this.state = {
            context: props.context,
            octave: 3,
            notes: ["C", "D", "D", "E", "E", "F", "G", "G", "A", "A", "B", "B", "C", "D", "D", "E", "E", "F", "G", "G"],
            keyboardEnabled: props.keyboardEnabled,
            toggleKeyboard: props.toggleFunction
        };

        this.keyDown = this.keyDown.bind(this);
        this.keyUp = this.keyUp.bind(this);
        this.updateOctave = this.updateOctave.bind(this);
    }

    updateSynthState(){
      let componentState = {};
      for (var key in this.state) {
        if ( typeof(this.state[key]) !== 'object' ) {
          componentState[key] = this.state[key];
        }
      }
      let label = "Keyboard";
      window.synfesizer.presetState[label] = componentState;
    }

    componentDidUpdate(){
      this.updateSynthState();
    }
    componentWillReceiveProps(props){
      if (props.keyboardEnabled !== this.state.keyboardEnabled) {
        this.setState({keyboardEnabled: props.keyboardEnabled});
      }
    }
    keyDown(event){
        if(!!event.target.dataset.note){
          let note = event.target.dataset.note + " " + event.target.dataset.octave;
          this.props.play(note);
        }
    }

    keyUp(event){
        if(!!event.target.dataset.note){
            let note = null;
            this.props.play(note);
        }
    }

    updateOctave(event){
        this.setState({octave: parseInt(event.target.value, 10) });
    }

    render() {
        const KeyboardKey = (props)=>{
          return <div className={props.note} onTouchStart={this.keyDown} onMouseDown={this.keyDown} onTouchEnd={this.keyUp} onMouseUp={this.keyUp} key={props.key} data-note={props.note} data-octave={props.octave}>{props.note}<sub>{props.octave}</sub></div>;
        };
        return (
            <div className={`keys  ${this.state.keyboardEnabled ? "enabled": "disabled"}`}>
            <button className="toggle-keyboard" onClick={this.state.toggleKeyboard} alt="Toggle the on screen keyboard" title="Toggle the on screen keyboard" tabIndex="-1"></button>
                <div className='octave'>
                  <label>Keyboard Octave</label>
                  <br/>
                  <input className='octaveSelector' type='range' min='0' max='9' step='1' onChange={this.updateOctave.bind(this)} defaultValue={this.state.octave} tabIndex="-1"/>
                </div>
                <div className="keyboard">
                    {this.state.notes.map(function(note, index){
                      var octave = index < 12 ? this.state.octave : this.state.octave + 1;
                      return KeyboardKey({key:index, note: note, octave: octave });
                    }.bind(this))}
                </div>
            </div>
        );
    }
}
export default Keyboard;



