import React, { Component } from 'react';
import './midi_keyboard.css';
import frequencyTable from '../shared/frequency_table.js';

class MidiKeyboard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      context: props.context,
      frequency: props.frequency,
      frequency_table: frequencyTable,
      available_midi_devices: [],
      selected_midi_device: '',
      last_played_note: null,
      toggleKeyboard: props.toggleFunction,
      keyboardEnabled: props.keyboardEnabled
    };

    this.frequencyLookup = this.frequencyLookup.bind(this);
    this.onMidiSuccess = this.onMidiSuccess.bind(this);
    this.onMidiFailure = this.onMidiFailure.bind(this);
    this.onMidiMessage = this.onMidiMessage.bind(this);
    this.selectMidiDevice = this.selectMidiDevice.bind(this);
    this.refreshMidiDevices = this.refreshMidiDevices.bind(this);
  }

  frequencyLookup(note){
      return this.state.frequency_table.find(function(row){
          if(parseInt(row.midi, 10) === note){
              return row;
          } else {
              return null;
          }
      });
  }

  onMidiSuccess(midiAccess) {
    // When we get a succesful response, run this code
    // this is our raw MIDI data, inputs, outputs, and sysex status
    let midi = midiAccess,
        available = [];

    midi.inputs.forEach(function(input){
      available.push(input);
    });

    if(available.length > 0){
      this.setState({available_midi_devices: available}, function(){
        if(!this.state.selected_midi_device){
          this.setState({selected_midi_device: this.state.available_midi_devices[0]}, function(){
            this.setState({selected_midi_device: Object.assign(this.state.selected_midi_device, {onmidimessage: this.onMidiMessage}) }, function(){
              // Should hide or keep hiding keyboard.
              return this.state.keyboardEnabled ? this.state.toggleKeyboard(): false ;
            });
          });
        }
      });
    } else {
      this.setState({available_midi_devices: []}, function(){
        this.setState({selected_midi_device: null}, function(){
          // Should show or keep showing keyboard.
          return this.state.keyboardEnabled ? false : this.state.toggleKeyboard();
        });
      });
    }
  }

  onMidiFailure(error) {
      // When we get a failed response, run this code
      console.log("No access to MIDI devices or your browser doesn't support WebMIDI API. Please use WebMIDIAPIShim " + error);
  }

  onMidiMessage(message) {
    let action = message.data[0],
        midi_note = message.data[1],
        velocity = message.data[2];

    if (this.state.selected_midi_device.name === message.currentTarget.name) {
      if(action === 144 || action === 128){
        let note = this.frequencyLookup(midi_note);
        if(action === 144 && velocity > 0){
          // Play Note
          this.setState({last_played_note: note});
          this.props.play(note['note'] + " " + note['octave']);
        }
        if((action === 128 || 144) && velocity === 0){
          // Release
          if (this.state.last_played_note === note){
            this.props.play(null);
          }
        }
      }
    }
  }

  getMidiDevice(){
    // Request MIDI access
    if (navigator.requestMIDIAccess) {
      navigator.requestMIDIAccess({
        sysex: false
      }).then(this.onMidiSuccess, this.onMidiFailure);
    } else {
      this.state.toggleKeyboard();
      alert("No MIDI support in your browser.");
    }
  }

  componentDidMount(){
    this.getMidiDevice();
  }

  componentWillReceiveProps(props){
    if(props.keyboardEnabled !== this.state.keyboardEnabled){
      this.setState({keyboardEnabled: props.keyboardEnabled});
    }
  }

  selectMidiDevice(event){
    if(this.state.selected_midi_device){
      this.setState({selected_midi_device: {onmidimessage: null}});
    }
    this.state.available_midi_devices.find(function(device){
      if(device.name === event.target.value){
        this.setState( {selected_midi_device: device});
        this.getMidiDevice();
      }
    }.bind(this));
  }

  refreshMidiDevices(event){
    this.getMidiDevice();
  }

  render(){
    const MidiDevice = (props) => {
      if(this.state.selected_midi_device === props.device){
        return <option selected key={props.device.id} value={props.device.name}>{props.device.name}</option>;
      } else {
        return <option key={props.device.id} value={props.device.name}>{props.device.name}</option>;
      }
    };

    return (
        <div className="midi">
          <label>Select Midi Device / On-Screen Keys</label>
          <select value={this.state.selected_midi_device ? this.state.selected_midi_device.name : ""} onChange={this.selectMidiDevice}>
            <option>No Midi Device Detected</option>;
            { this.state.available_midi_devices.map( function(device, index){
              return MidiDevice({key: index, device: device});
            })}
          </select>
          <button className="refresh_midi" onClick={this.refreshMidiDevices} alt="Refresh Midi Device List" title="Refresh Midi Device List">  </button>
        <button className="toggle-keyboard" onClick={this.state.toggleKeyboard} alt="Toggle the on screen keyboard" title="Toggle the on screen keyboard"> &nbsp; </button>
        </div>
    );
  };
}
export default MidiKeyboard;



