import React, { Component } from 'react';
import './preset_uploader.css';

class PresetUploader extends Component {
  constructor (props) {
    super(props);
    this.state = {
      preset: null,
      presets: props.presets,
      updatePresetsFromUpload: props.updatePresetsFromUpload
    };

    this.uploadFile = this.uploadFile.bind(this);
  }

  loadToStorage(preset){
    let preset_exists = this.state.presets.some((local_preset) => {return local_preset.name === preset.name;});;
    if (preset_exists) {
      let confirm = window.confirm("A preset with the name " + preset.name + "already exists. Do you want to overwrite it?");
      if (confirm) {
        window.localStorage.setItem(preset.name,JSON.stringify(preset.settings));
        this.state.updatePresetsFromUpload(preset.name);
      }
    } else {
      window.localStorage.setItem(preset.name,JSON.stringify(preset.settings));
      this.state.updatePresetsFromUpload(preset.name);
    }
  }

  //Events
  uploadFile(event){
    let input = event.target,
        file = input.files[0],
        preset,
        reader;

    if (file.type.match("application/json")) {
      reader = new FileReader();
      reader.onload = function(event) {
        preset = JSON.parse(event.target.result);
        this.loadToStorage(preset);
        input.value = null;
      }.bind(this);
      reader.readAsText(file);
    }
  }
  clickUpload() {
    let input = document.getElementById('fileInput');
    input.click();
  }

  render(){
    return(
        <div className="preset-uploader">
            <input type="file" id="fileInput" onChange={this.uploadFile}/>
        <button className="file-upload" onClick={this.clickUpload} title="Upload a Preset .json File" alt="Upload a Preset .json File">&uarr;</button>
       </div>
    );
  }
}
export default PresetUploader;



