import React, { Component } from 'react';
import './lfo.css';

class LFO extends Component {
    constructor(props) {
        super(props);

        this.state = {
          output: props.output,
          label: props.label,
          lfo1: props.context.createOscillator(),
          lfoTypes: ["sine", "triangle", "sawtooth", "square"],
          lfoType: 3,
          lfoRate: 5,
          context: props.context,
          lfoGain: props.context.createGain(),
          preset: props.preset
        };

        this.switchLfoType = this.switchLfoType.bind(this);
        this.changeLfoRate = this.changeLfoRate.bind(this);
    }

    updateSynthState(){
      let componentState = {};
      for (var key in this.state) {
        if ( typeof(this.state[key]) !== 'object' ) {
          componentState[key] = this.state[key];
        }
      }
      let label = componentState.label;
      window.synfesizer.presetState[label] = componentState;
    }

    componentDidUpdate(){
      this.updateSynthState();
    }

    componentDidMount(){
      this.updateSynthState();
    }

    setLfo() {
      let lfo1 = this.state.lfo1,
          lfoGain = this.state.lfoGain;

      lfo1.frequency.setTargetAtTime(this.state.lfoRate, this.state.context.currentTime, 0);
      lfo1.type = this.state.lfoTypes[this.state.lfoType];
      lfo1.connect(this.state.lfoGain);
      lfoGain.gain.setTargetAtTime(35, this.state.context.currentTime, 0);
      lfoGain.connect(this.state.output);
    }

    initiate(){
      this.setLfo();
      this.state.lfo1.start(0);
    }

    componentWillMount(){
      this.initiate();
    }

    componentWillReceiveProps(props){
      if (this.state.preset !== props.preset) {
        this.setState({preset: props.preset}, function(){
          let settings = this.props.preset.settings[this.props.label];
          this.setState(settings, function(){
            this.setLfo();
          }.bind(this));
        }.bind(this));
      }
    }


    //EventHandlers

    switchLfoType(event){
      this.setState({lfoType:parseInt(event.target.value, 0)});
      this.setLfo();
    }

    changeLfoRate(event){
      this.setState({lfoRate:parseFloat(event.target.value, 0)});
      this.setLfo();
    }

    // Render
    render() {
        return (
          <div className="lfo">
            <h2>{this.state.label}</h2>
              <div className="lfo-osctype-labels">
                <label>Sine</label>
                <label>Triangle</label>
                <label>Saw</label>
                <label>Square</label>
              </div>
              <input className="lfo-osctype" type="range" min='0' max='3' step='1' onChange={this.switchLfoType} value={ this.state.lfoType } />
              <label>Rate ({this.state.lfoRate} Hz)</label>
              <input className='lfo-rate' type='range' step='0.01' min='0.01' max='59.00' onChange={this.changeLfoRate} value={this.state.lfoRate} />
          </div>
        );
    }
}
export default LFO;



