import React, { Component } from 'react';
import MasterVolume from './components/master_volume/master_volume';
import Oscilloscope from './components/oscilloscope/oscilloscope';
import Lfo from './components/lfo/lfo';
import Vco from './components/vco/vco';
import LowPassFilter from './components/low_pass_filter/low_pass_filter';
import HighPassFilter from './components/high_pass_filter/high_pass_filter';
import Keyboard from './components/keyboard/keyboard';
import MidiKeyboard from './components/midi_keyboard/midi_keyboard';
import PresetUploader from './components/preset_uploader/preset_uploader';
import DefaultPresets from './components/shared/default_presets';
import './App.css';

window['synfesizer'] = {
  presetState: {}
};

class App extends Component {
    constructor(props) {
        super(props);

        let AudioContext = window.AudioContext // Default
              || window.webkitAudioContext // Safari and old versions of Chrome
              || false;

        this.state = {
          context: new AudioContext(),
          note: null,
          master: null,
          lfo1Bridge: null,
          lfo2Bridge: null,
          lpFilterBridge: null,
          hpFilterBridge: null,
          vcoBridge: null,
          default_presets: DefaultPresets,
          presets: [],
          selectedPreset: null,
          newPresetName: "",
          keyboardEnabled: false,
          caffeinated: false
        };

        this.savePreset = this.savePreset.bind(this);
        this.cancelSavePreset = this.cancelSavePreset.bind(this);
        this.loadPreset = this.loadPreset.bind(this);
        this.downloadPreset = this.downloadPreset.bind(this);
        this.updatePresetsFromUpload = this.updatePresetsFromUpload.bind(this);
        this.updateNewPresetName = this.updateNewPresetName.bind(this);
        this.toggleKeyboard = this.toggleKeyboard.bind(this);
        this.toggleCaffeine = this.toggleCaffeine.bind(this);
    }

    createMaster(){
        let master = this.state.context.createGain();
        this.setState({master: master}, function(){
          master.connect(this.state.context.destination);
        });
    }

    createLfo1Bridge(){
      let bridge = this.state.context.createGain();
      this.setState({lfo1Bridge: bridge});
    }

    createLfo2Bridge(){
      let bridge = this.state.context.createGain();
      this.setState({lfo2Bridge: bridge});
    }

    createLpFilterBridge(){
        let bridge = this.state.context.createGain();
        this.setState({lpFilterBridge: bridge});
    }

    createHpFilterBridge(){
      let bridge = this.state.context.createGain();
      // This the final Bridge before Master 0.37 is just a magic number that seems to yield the most reasonable volumes
      bridge.gain.setTargetAtTime(0.37, this.state.context.currentTime, 0 );
      this.setState({hpFilterBridge: bridge}, function(){
        bridge.connect(this.state.master);
      });
    }

    createVcoBridge(){
        let bridge = this.state.context.createGain();
        this.setState({vcoBridge: bridge});
    }

    loadDefaultPresets(){
      if (window.localStorage.getItem('presets_loaded') !== "true") {
        this.state.default_presets.forEach( function(preset){
          window.localStorage.setItem(preset.name,JSON.stringify(preset.settings));
        });
        window.localStorage.setItem("presets_loaded", "true");
      }
      this.setState({presets: this.getAllPresets()}, function(){
        if (!this.state.selectedPreset) {
          let selected_preset = this.state.presets.filter( function( preset ) {
            return preset.name === "Default" ? preset : false;
          });
          selected_preset = {name: selected_preset[0].name, settings: JSON.parse(selected_preset[0].settings)};
          this.setState({selectedPreset: selected_preset}, function(){
            this.setState({newPresetName: this.state.selectedPreset.name});
          });
        }
      });
    }

    componentWillMount(){
      this.loadDefaultPresets();
      this.createMaster();
      this.createLfo1Bridge();
      this.createLfo2Bridge();
      this.createVcoBridge();
      this.createLpFilterBridge();
      this.createHpFilterBridge();
    }

    componentDidMount(){
      // Woohoo Caffeine Mode! 
      window.onfocus = ()=> {
        if (!this.state.caffeinated) {
          this.state.context.resume();
        }
      };

      window.onblur = ()=> {
        if (!this.state.caffeinated) {
          this.state.context.suspend();
        }
      };
    }

    getAllPresets() {
        var values = [],
            keys = Object.keys(localStorage),
            i = keys.length;
        while ( i-- ) {
          let obj ={};
          obj['name'] = keys[i];
          obj['settings'] = window.localStorage.getItem(keys[i]);
          if(obj['name'] !== "presets_loaded"){
            values.push( obj );
          }
        }
        return values;
    }

    updatePresetsFromUpload(preset_name){
      this.setState({presets: this.getAllPresets()}, function(){
        let selected_preset = this.state.presets.filter( function( local_preset ) {
          return local_preset.name === preset_name ? local_preset : false;
        });
        selected_preset = {name: selected_preset[0].name, settings: JSON.parse(selected_preset[0].settings)};
        this.setState({selectedPreset: selected_preset});
      }.bind(this));
    }

    // Events
    toggleKeyboard(){
      let body = document.getElementsByTagName('body')[0];
      if (this.state.keyboardEnabled) {
        body.classList.remove('keyboard-enabled');
        this.setState({keyboardEnabled: false});
      } else {
        body.classList.add('keyboard-enabled');
        this.setState({keyboardEnabled: true});
      }
    }

    playNote(note) {
        this.setState({ note: note });
    }

    showSaveDialog(){
      let dialog = document.getElementsByClassName('new-preset-name')[0];
      window.requestAnimationFrame( ()=> {
        dialog.classList.add('visible');
      });
    }

    hideSaveDialog(){
      let dialog = document.getElementsByClassName('new-preset-name')[0];
      window.requestAnimationFrame( ()=> {
        dialog.classList.remove('visible');
      });
    }

    savePreset(){
      let prompted_name = this.state.newPresetName;
      let preset = JSON.stringify(window.synfesizer.presetState);
      window.localStorage.setItem(prompted_name,preset);

      this.setState({presets: this.getAllPresets()}, function(){
        let selected_preset = this.state.presets.filter( function( preset ) {
          return preset.name === prompted_name ? preset : false;
        });
        selected_preset = {name: selected_preset[0].name, settings: JSON.parse(selected_preset[0].settings)};
        this.setState({selectedPreset: selected_preset}, function(){
          this.setState({newPresetName: this.state.selectedPreset.name}, function(){
            this.hideSaveDialog();
          });
        });
      }.bind(this));
    }

    cancelSavePreset(){
      this.hideSaveDialog();
      this.setState({newPresetName: this.state.selectedPreset.name}, function(){
      });
    }

    loadPreset(event){
      let selected_preset = this.state.presets.filter( function( preset ) {
        return preset.name === event.target.value ? preset : false;
      });
      selected_preset = {name: selected_preset[0].name, settings: JSON.parse(selected_preset[0].settings)};
      this.setState({selectedPreset: selected_preset}, function(){
        this.setState({newPresetName: selected_preset.name});
      });
    }

    downloadPreset(){
      if (!!this.state.selectedPreset) {
        let element = document.createElement('a'),
            text = JSON.stringify(this.state.selectedPreset),
            filename = this.state.selectedPreset.name;

        element.setAttribute('href', 'data:application/octet-stream;charset=utf-8,' + encodeURIComponent(text));
        element.setAttribute('download', filename + ".json");
        element.style.display = 'none';
        document.body.appendChild(element);
        element.click();
        document.body.removeChild(element);
      }
    }

    updateNewPresetName(event){
      this.setState({newPresetName: event.target.value});
    }

    toggleCaffeine(event){
      this.state.caffeinated ? this.setState({caffeinated: false}) : this.setState({caffeinated: true});
    }

    render() {
        const Preset = (props) => {
          if(this.state.selectedPreset === props.preset){
            return <option selected key={props.key} value={props.preset.name}>{props.preset.name}</option>;
          } else {
            return <option key={props.key} value={props.preset.name}>{props.preset.name}</option>;
          }
        };

        const DownloadButton = (props) => {
          if(!!this.state.selectedPreset){
            return <button className="download" onClick={this.downloadPreset} title="Download current Preset" alt="Download current Preset">&darr;</button>;
          }
          return false;
        };

      const CaffeineButton = (props) => {
        return <div className="caffeine">
          <label> !!!</label>
          <button className={this.state.caffeinated ? 'on' : 'off'} onClick={this.toggleCaffeine} title="Toggle Caffeinated mode. Stay awake even when focus is lost" alt="Toggle Caffeinated mode. Stay awake even when focus is lost"></button>
          </div>;
      };

        return (
          <div className="app">
            <div className="titlebar"></div>
            <div className="toolbar">
              <div className="logo-holder">
                <div className="logo">&nbsp;</div>
                &nbsp;
              </div>
              <div className="presets">
                <label> Select Preset </label>
                <select value={this.state.selectedPreset ? this.state.selectedPreset.name : ""} onChange={this.loadPreset}>
                    {
                      this.state.presets.map( function(preset, index){
                        return Preset({key: index, preset: preset});
                      }).sort(function(a, b){
                        if(a.name > b.name) return 1;
                        if(a.name < b.name) return -1;
                        return 0;
                      }).reverse()
                    }
                </select>
                <button className="save" onClick={this.showSaveDialog} title="Save as New Preset" alt="Save as New Preset"> + </button>
                <PresetUploader presets={this.state.presets} updatePresetsFromUpload={this.updatePresetsFromUpload}/>
                <DownloadButton/>
                <div className="new-preset-name">
                  <label> Save Preset As </label>
                  <input type="text" className="preset-name" value={this.state.newPresetName} onChange={this.updateNewPresetName} tabIndex="-1"/>
                  <button onClick={this.savePreset} tabIndex="-1">Save</button>
                  <button onClick={this.cancelSavePreset} tabIndex="-1">Cancel</button>
                </div>
              </div>
            <MidiKeyboard play={this.playNote.bind(this)} toggleFunction={this.toggleKeyboard} keyboardEnabled={this.state.keyboardEnabled}/>
              <Keyboard play={this.playNote.bind(this)} toggleFunction={this.toggleKeyboard} keyboardEnabled={this.state.keyboardEnabled}/>
              <MasterVolume master={this.state.master} preset={this.state.selectedPreset} context={this.state.context}/>
              <CaffeineButton/>
            </div>
            <div className="shaping">
              <div className="lfos">
                <Oscilloscope context={this.state.context} source={this.state.master}/>
                <Lfo context={this.state.context} output={this.state.lfo1Bridge} preset={this.state.selectedPreset} label="LFO1"/>
                <Lfo context={this.state.context} output={this.state.lfo2Bridge} preset={this.state.selectedPreset} label="LFO2"/>
              </div>
              <LowPassFilter
                context={this.state.context}
                source={this.state.vcoBridge}
                output={this.state.lpFilterBridge}
                lfo1={this.state.lfo1Bridge}
                lfo2={this.state.lfo2Bridge}
                preset={this.state.selectedPreset}
                note={this.state.note}
              />
              <HighPassFilter
                context={this.state.context}
                source={this.state.lpFilterBridge}
                output={this.state.hpFilterBridge}
                lfo1={this.state.lfo1Bridge}
                lfo2={this.state.lfo2Bridge}
                preset={this.state.selectedPreset}
                note={this.state.note}
              />
            </div>
            <div className="oscillators">
              <Vco
                type='0'
                context={this.state.context}
                output={this.state.vcoBridge}
                note={this.state.note}
                label="VCO1"
                synthState={this.state.synthState}
                preset={this.state.selectedPreset}
              />
              <Vco
                type='1'
                context={this.state.context}
                output={this.state.vcoBridge}
                note={this.state.note}
                label="VCO2"
                synthState={this.state.synthState}
                preset={this.state.selectedPreset}
              />
              <Vco
                type='2'
                context={this.state.context}
                output={this.state.vcoBridge}
                note={this.state.note}
                label="VCO3"
                synthState={this.state.synthState}
                preset={this.state.selectedPreset}
              />
            </div>
          </div>
        );
    }
}
export default App;



