import React, { Component } from 'react';
import './oscilloscope.css';

class Oscilloscope extends Component {
    constructor(props) {
        super(props);
        this.state = {
          context: props.context,
          analyser: props.context.createAnalyser(),
          source: props.source,
          canvas: null,
          oscilloscope: null
        };

      this.initiate = this.initiate.bind(this);
      this.draw = this.draw.bind(this);
    }

    initiate(){
      this.state.source.connect(this.state.analyser);
    }

    componentDidMount(){
      this.setState({canvas: document.querySelector('.oscilloscope')}, function(){
        this.setState({oscilloscope: this.state.canvas.getContext('2d')}, function(){
          window.requestAnimationFrame(this.draw);
        });
      });
    }

    componentWillMount(){
      this.initiate();
    }

    draw() {
      let bufferLength = this.state.analyser.frequencyBinCount,
          dataArray = new Uint8Array(bufferLength),
          canvas_width = 300,
          canvas_height = 150,
          sliceWidth = canvas_width / bufferLength,
          x = 0,
          analyser = this.state.analyser,
          osc = this.state.oscilloscope;

      analyser.fftSize = 1024;
      osc.fillStyle = 'rgb(0, 0, 0)';
      osc.strokeStyle = '#db3434';
      osc.lineWidth = 2;

      analyser.getByteTimeDomainData(dataArray);
      osc.clearRect(0, 0, canvas_width, canvas_height);
      osc.fillRect(0, 0, canvas_width, canvas_height);
      osc.beginPath();

      setTimeout( () =>{
        window.requestAnimationFrame(this.draw);
      }, 16);

      for(var i = 0; i < bufferLength; i++) {
        var v = dataArray[i] / 128.0;
        var y = v * canvas_height / 2;

        if(i === 0) {
          this.state.oscilloscope.moveTo(x, y);
        } else {
          this.state.oscilloscope.lineTo(x, y);
        }
        x += sliceWidth;
      };

      this.state.oscilloscope.lineTo(this.state.canvas.width, this.state.canvas.height / 2);
      this.state.oscilloscope.stroke();
    }

    // Render
    render() {
        return (
          <div className="oscilloscope-holder">
            <canvas className="oscilloscope"></canvas>
          </div>
        );
    }
}
export default Oscilloscope;



