import React, { Component } from 'react';
import './envelope.css';

class Envelope extends Component {
  constructor(props) {
    super(props);

    this.state = {
      label: props.label,
      canvas: null,
      envelope: null,
      width: props.width,
      height: props.height,
      attack: this.calcAttack(props.attack),
      decay:  this.calcDecay(props.decay),
      sustain: this.calcSustain(props.sustain),
      release: this.calcRelease(props.release),
      peak: props.peak,
      peaks: []
    };
  }

  calcAttack(attack){
    return Math.round( ((attack / 5) * 100) * .40);
  }

  calcDecay(decay){
    return Math.round( ((decay / 5) * 100) * .40);
  }

  calcSustain(sustain){
    return Math.round( 100 - ((sustain / 5) * 100 ));
  }

  calcRelease(release){
    return Math.round( ((release / 12) * 100) * .20 );
  }

  calcPeak(peak, amount){
    return Math.round( (amount / peak) * 100);
  }

  draw() {
    let canvas_width = this.state.width,
        canvas_height = this.state.height,
        env = this.state.envelope,
        attack = this.state.attack/100 *canvas_width,
        decay = this.state.decay/100 *canvas_width,
        sustain = this.state.sustain,
        release = this.state.release/100 *canvas_width;

    env.clearRect(0, 0, canvas_width, canvas_height);
    env.fillRect(0, 0, canvas_width, canvas_height);

    this.state.peaks.reverse().forEach( (peak, index) =>{
      let color = index ? '#B22C2C' : '#444' ;
      env.fillStyle = 'rgb(0, 0, 0)';
      env.strokeStyle = color;
      env.lineWidth = 2;
      env.beginPath();
      env.moveTo(0, canvas_height);
      env.lineTo(attack, canvas_height*(peak/100) );
      env.lineTo(attack + decay, sustain + canvas_height*(peak/100));
      env.lineTo(release + attack + decay, canvas_height);
      env.stroke();
    });
  }

  componentDidUpdate(){
    requestAnimationFrame(this.draw.bind(this));
  }

  componentWillReceiveProps(props) {
    this.setState({peaks: props.peaks.map( (p) => { return 100 - Math.round(p / props.peak * 100) ; }) });
    this.setState({peak: props.peak});
    this.setState({ attack: this.calcAttack(props.attack)});
    this.setState({ decay: this.calcDecay(props.decay)});
    this.setState({ sustain: this.calcSustain(props.sustain)});
    this.setState({ release: this.calcRelease(props.release)});
  }

  componentDidMount(){
    this.setState({canvas: document.querySelector('.' + this.state.label)}, function(){
      this.setState({envelope: this.state.canvas.getContext('2d')}, function(){
        this.draw();
      });
    });
  }

  render() {
    return (
        <div className="envelope">
          <canvas className={this.state.label} width={this.state.width} height={this.state.height}></canvas>
        </div>
    );
  }
}
export default Envelope;



