import React, { Component } from 'react';
import './master_volume.css';

class MasterVolume extends Component {
    constructor(props) {
        super(props);

        this.state = {
          context: this.props.context,
          masterVolume: 0.25,
          preMuteVolume: null,
          master: props.master,
          preset: props.preset
        };

        this.updateMasterVolume = this.updateMasterVolume.bind(this);
        this.muteUnMuteMaster= this.muteUnMuteMaster.bind(this);
    }

    updateSynthState(){
      let componentState = {};
      for (var key in this.state) {
        if ( typeof(this.state[key]) !== 'object' ) {
          componentState[key] = this.state[key];
        }
      }
      window.synfesizer.presetState["MasterVolume"] = componentState;
    }

    componentDidUpdate(){
      this.updateSynthState();
      this.initialize();
    }


    componentWillReceiveProps(props) {
      if (this.state.preset !== props.preset) {
        this.setState({preset: props.preset}, function(){
          this.setState({masterVolume: props.preset.settings.MasterVolume.masterVolume}, function(){
            this.initialize();
          }.bind(this));
        }.bind(this));
      }
    }

    initialize(){
      let master = this.state.master;
      master.gain.setTargetAtTime(this.state.masterVolume, this.state.context.currentTime, 0);
    }

    componentDidMount(){
      this.updateSynthState();
      this.initialize();
    }

    // Events
    updateMasterVolume(event){
      this.setState({masterVolume: parseFloat(event.target.value)}, function(){
        this.initialize();
      });
    }

    muteUnMuteMaster(event){
      if(this.state.master.gain.value > 0){
        this.setState({preMuteVolume: this.state.masterVolume},function (){
          this.setState({masterVolume: 0}, function(){
            this.initialize();
          });
        });
      }else {
        this.setState({masterVolume: this.state.preMuteVolume}, function(){
          this.initialize();
        });
      }
    }

    render() {
      return <div className="master-volume">
        <label>Master Volume ({Math.round(this.state.masterVolume * 100)}%)</label>
        <input className='volume' type='range' min='0' max='1' step='0.01' value={this.state.masterVolume} onChange={this.updateMasterVolume.bind(this)} />
        <button className={this.state.masterVolume > 0 ? "on": "off"} onClick={this.muteUnMuteMaster} alt="Mute / Unmute Master Volume" title="Mute / Unmute Master Volume"></button>
      </div>;
    }
}
export default MasterVolume;



