import React from 'react';
import './pitch_control.css';

class PitchControl extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      pitch: props.pitch,
      octave: 3,
      pitchOffset: 0,
      pitch_range: ["C","D","D","E","E","F","G","G","A","A","B","B"]
    };

    this.offsetPitchUp = this.offsetPitchUp.bind(this);
    this.offsetPitchDown = this.offsetPitchDown.bind(this);
  }

  offsetPitchUp(event){
    if (this.state.pitch === "B 5") {
      event.preventDefault();
      return false;
    }

    let offset = this.state.pitchOffset;
    if (offset >= 11) {
      offset = -1;
      this.setState({octave: this.state.octave + 1});
    }
    return this.setState({pitchOffset: offset + 1}, function(){
      this.setState({pitch: this.state.pitch_range[this.state.pitchOffset] + " " + this.state.octave }, function(){
        this.props.setPitch(this.state.pitch);
      });
    });
  }

  offsetPitchDown(event){
    if (this.state.pitch === "C 0") {
      event.preventDefault();
      return false;
    }

    let offset = this.state.pitchOffset;
    if (offset <= 0) {
      offset = 12;
      this.setState({octave: this.state.octave - 1});
    }
    return this.setState({pitchOffset: offset - 1}, function(){
      this.setState({pitch: this.state.pitch_range[this.state.pitchOffset] + " " + this.state.octave }, function(){
        this.props.setPitch(this.state.pitch);
      });
    });
  }

  componentWillReceiveProps(props){
    if(props.pitch){
      this.setState({pitch: props.pitch}, function(){
        this.setState({octave: +this.state.pitch.split(' ')[1]}, function(){
          this.setState({pitchOffset: this.state.pitch_range.indexOf(this.state.pitch.split(' ')[0])});
        });
      });
    }
  }

  render() {
    return (
      <div className="pitch-control">
        <h2>Pitch</h2>
        <button className={this.state.pitch === "B 5" ?  "off" : "on"} onClick={this.offsetPitchUp} disabled={this.state.pitchOffset < 35 ? false : true}>&uarr;</button>
        <div className="pitchOffset">{this.state.pitch}</div>
        <button className={this.state.pitch === "C 0" ? "off" : "on"} onClick={this.offsetPitchDown} disabled={this.state.pitchOffset > -48 ? false : true}>&darr;</button>
      </div>
    );
  }
}
export default PitchControl;



