import './vco.css';
import React from 'react';
import Envelope from '../envelope/envelope';
import PitchControl from '../pitch_control/pitch_control';
import frequencyTable from '../shared/frequency_table.js';


class Vco extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
          type: props.type,
          types: ["sine", "triangle", "sawtooth", "square"],
          context: props.context,
          output: props.output,

          label: props.label,

          osc1: props.context.createOscillator(),
          vca1: props.context.createGain(),
          vca2: props.context.createGain(),
          osc1Master: props.context.createGain(),
          oscMaster: props.context.createGain(),
          oscMasterVolume: 0.5,
          oscVolume: 1,

          subosc1: props.context.createOscillator(),
          subosc1Master: props.context.createGain(),
          subVolume: 1,
          subLatch: false,


          notes: ["C", "D", "D", "E", "E", "F", "G", "G", "A", "A", "B", "B"],
          frequency_table: frequencyTable,
          note: props.note,
          frequency: 0,
          pitch: "C 3",

          attack: 0.1,
          decay: 0.75,
          sustain: 1,
          release: 1,
          peak: 1,

          preset: props.preset
        };

        this.switchOscType = this.switchOscType.bind(this);
        this.muteUnMuteOscillator = this.muteUnMuteOscillator.bind(this);
        this.muteUnMuteSubOscillator = this.muteUnMuteSubOscillator.bind(this);
        this.latchSubOscillator = this.latchSubOscillator.bind(this);
        this.changeOscVolume = this.changeOscVolume.bind(this);
        this.changeSubVolume = this.changeSubVolume.bind(this);
        this.frequencyLookup = this.frequencyLookup.bind(this);
        this.setPitch = this.setPitch.bind(this);
    }

    setPitch(pitch){
      this.setState({pitch: pitch});
    }

    updateSynthState() {
      let componentState = {};
      for (var key in this.state) {
        if ( typeof(this.state[key]) !== 'object' ) {
          componentState[key] = this.state[key];
        }
      }
      let label = componentState.label;
      window.synfesizer.presetState[label] = componentState;
    }

    frequencyLookup(note, octave){
      return this.state.frequency_table.find( function(n) {
        if(n.note === note && parseInt(n.octave, 10) === parseInt(octave, 10)){
          return n;
        } else {
          return null;
        }
      });
    }

    noteOn(frequency){
      let attack = this.state.attack,
          decay = this.state.decay,
          latency = 0.004,
          sustain = this.state.sustain;


      this.state.vca1.gain.cancelScheduledValues(0);
      this.state.vca2.gain.cancelScheduledValues(0);

      this.state.vca1.gain.setTargetAtTime(0, this.state.context.currentTime,  latency );
      this.state.vca2.gain.setTargetAtTime(0, this.state.context.currentTime,  latency );

      this.setState({frequency: frequency}, function(){
        this.state.vca1.gain.linearRampToValueAtTime(5, this.state.context.currentTime + latency + attack);
        this.state.vca2.gain.linearRampToValueAtTime(5, this.state.context.currentTime + latency + attack);
        this.state.vca1.gain.linearRampToValueAtTime(sustain, this.state.context.currentTime + latency + attack + decay);
        this.state.vca2.gain.linearRampToValueAtTime(sustain, this.state.context.currentTime + latency + attack + decay);
      });
    }

    noteOff(){
      let release = this.state.release;

      this.state.vca1.gain.cancelScheduledValues(0);
      this.state.vca2.gain.cancelScheduledValues(0);

      this.state.vca1.gain.linearRampToValueAtTime(0, this.state.context.currentTime + release);
      this.state.vca2.gain.linearRampToValueAtTime(0, this.state.context.currentTime + release);
    }

    componentWillReceiveProps(props){
      if (!!props.note) {
        let pitch = this.state.pitch.split(' '),
            pitch_note = pitch[0],
            pitch_octave = +pitch[1],
            pitch_offset,
            played  = props.note.split(' '),
            played_note  = played[0],
            played_octave  = +played[1],
            played_position,
            base = "C 3".split(' '),
            base_octave = +base[1],
            notes = this.state.notes,
            note,
            frequencyLookup = this.frequencyLookup;

        pitch_offset = notes.map(function(note, index){ return pitch_note === note ? index + "" : "";});
        played_position = notes.map(function(note, index){ return played_note === note ? index + "" : "";});
        pitch_offset = +pitch_offset.filter(Boolean)[0];
        played_position = +played_position.filter(Boolean)[0];

        let octave = played_octave - base_octave + pitch_octave;

        let getNote = (position, octave) => {
          if ( position >= 12 ) {
            return getNote(position - 12, octave + 1);
          } else {
            if (octave > 9) {
              octave = 9;
            }
            if(octave < 0) {
              octave = 0;
            }
            return frequencyLookup(notes[position], octave);
          }
        };

        if (pitch_offset === 0) {
          note = frequencyLookup(played_note, octave);
        } else {
          note = getNote(played_position + pitch_offset, octave);
        }
        this.noteOn(note.frequency);
      } else {
        this.noteOff();
      }

      if (this.state.preset !== props.preset) {
        this.setState({preset: props.preset}, function(){
          let settings = this.props.preset.settings[this.props.label];
          this.setState(settings, function(){
            this.setOscillator();
          }.bind(this));
        }.bind(this));
      }
    }

    //Instance Methods
    setOscillator() {
      let osc1 = this.state.osc1,
          subosc1 = this.state.subosc1,
          osc1Master = this.state.osc1Master,
          subosc1Master = this.state.subosc1Master,
          oscMaster = this.state.oscMaster;

      osc1.frequency.setTargetAtTime( this.state.frequency, this.state.context.currentTime, 0);
      osc1.type = this.state.types[this.state.type];
      subosc1.frequency.setTargetAtTime(  this.state.frequency / 2, this.state.context.currentTime, 0);
      subosc1.type = this.state.subLatch ? this.state.types[this.state.type] : "sine";
      osc1Master.gain.setTargetAtTime( this.state.oscVolume, this.state.context.currentTime, 0);
      subosc1Master.gain.setTargetAtTime( this.state.subVolume, this.state.context.currentTime, 0);
      oscMaster.gain.setTargetAtTime( this.state.oscMasterVolume, this.state.context.currentTime, 0);
    }

    makeConnections(){
        this.state.osc1.connect(this.state.vca1);
        this.state.subosc1.connect(this.state.vca2);
        this.state.vca1.connect(this.state.osc1Master);
        this.state.vca2.connect(this.state.subosc1Master);
        this.state.osc1Master.connect(this.state.oscMaster);
        this.state.subosc1Master.connect(this.state.oscMaster);
        this.state.oscMaster.connect(this.state.output);
    }

    startOscillators(){
        this.state.osc1.start(this.state.context.currentTime);
        this.state.subosc1.start(this.state.context.currentTime);
    }

    componentDidMount(){
        this.setOscillator();
        this.makeConnections();
        this.startOscillators();
        this.updateSynthState();
    }

    componentDidUpdate(){
      this.setOscillator();
      this.updateSynthState();
    }

    // Event Handlers
    updateAttack(event){
        this.setState({attack: parseFloat(event.target.value)});
    }

    updateDecay(event){
        this.setState({decay: parseFloat(event.target.value)});
    }

    updateSustain(event){
        this.setState({sustain: parseFloat(event.target.value)});
    }

    updateRelease(event){
        this.setState({release: parseFloat(event.target.value)});
    }

    switchOscType(event){
        this.setState({type: event.target.value}, function(){
            this.setOscillator();
        });
    }

    muteUnMuteOscillator(){
        if(this.state.osc1Master.gain.value > 0){
            this.setState({oscVolume: 0}, function(){
                this.setOscillator();
            });
        }else {
            this.setState({oscVolume: 1}, function(){
              this.setOscillator();
            });
        }
    }

    muteUnMuteSubOscillator(){
        if(this.state.subosc1Master.gain.value > 0){
            this.setState({subVolume: 0}, function(){
              this.setOscillator();
            });
        }else {
            this.setState({subVolume: 1}, function(){
              this.setOscillator();
            });
        }
    }

    latchSubOscillator(){
        if(this.state.subLatch){
            this.setState({subLatch: false}, function(){
              this.setOscillator();
            });
        } else {
            this.setState({subLatch: true}, function(){
              this.setOscillator();
            });
        }
    }

    changeOscVolume(event){
      this.setState({oscVolume: parseFloat(event.target.value)}, function(){
        this.setOscillator();
      });
    }

    changeSubVolume(event){
      this.setState({subVolume: parseFloat(event.target.value)}, function(){
        this.setOscillator();
      });
    }

    // Render
    render() {
        return (
            <div className="vco-holder">
              <div className="vco">
                <div className="osc-volumes">
                  <h2>Mixer ({this.state.label}) </h2>
                  <label>Osc Volume ({Math.round(this.state.oscVolume * 100 )}%)</label>
                  <input className="osc-volume" type="range" min="0" max="1" step="0.01" onChange={this.changeOscVolume} value={ this.state.oscVolume }/>
                  <label>Sub Volume ({Math.round(this.state.subVolume * 100)}%)</label>
                  <input className="sub-volume" type="range" min="0" max="1" step="0.01" onChange={this.changeSubVolume} value={ this.state.subVolume}/>
                </div>
                <div className="buttons-knobs">
                  <h2>{this.state.label}</h2>
            <button className={this.state.oscVolume > 0 ? "on": "off"} onClick={this.muteUnMuteOscillator} alt="Mute this Oscillator" title="Mute this Oscillator">
            Osc {this.state.oscVolume > 0 ? "On": "Off"}
            </button>
            <button className={this.state.subVolume > 0 ? "on": "off"} onClick={this.muteUnMuteSubOscillator} alt="Mute this SubOscillator" title="Mute this SubOscillator">
            Sub {this.state.subVolume > 0 ? "On": "Off"}
            </button>
            <button className={this.state.subLatch ? "on": "off"} onClick={this.latchSubOscillator} alt="Latch the oscillator Type to the sub oscillator (normally a sine wave)" title="Latch the oscillator Type to the sub oscillator (normally a sine wave)">Sub Latch {this.state.subLatch ? "On": "Off"}</button>
                  <PitchControl setPitch={this.setPitch} pitch={this.state.pitch}/>
               </div>
                <div className="osctype-selector">
                    <input className="osctype" type="range" min="0" max="3" step="1" onChange={this.switchOscType} value={ this.state.type } orient='vertical'/>
                    <ul className="osctype-labels">
                        <li>Square</li>
                        <li>Saw</li>
                        <li>Triangle</li>
                        <li>Sine</li>
                    </ul>
                </div>
                <div className="adsr">
                    <h2>ENV</h2>
                    <Envelope
                      label={this.state.label + "_env"}
                      width="180"
                      height="70"
                      attack={this.state.attack}
                      decay={this.state.decay}
                      sustain={this.state.sustain}
                      release={this.state.release}
                      peak={this.state.peak}
                      peaks = {[this.state.oscVolume, this.state.subVolume]}
                    />
                    <label>Attack ({this.state.attack}ms)</label>
                    <input className='attack' type='range' min='0.0' max='5.0' step='0.01' onChange={this.updateAttack.bind(this)} value={this.state.attack} />
                    <label>Decay ({this.state.decay}ms)</label>
                    <input className='decay' type='range' min='0.01' max='5.0' step='0.01' onChange={this.updateDecay.bind(this)} value={this.state.decay} />
                    <label>Sustain ({Math.round(this.state.sustain/5 *100)}%)</label>
                    <input className='sustain' type='range' min='0' max='5.0' step='0.01' onChange={this.updateSustain.bind(this)} value={this.state.sustain}/>
                    <label>Release ({this.state.release}ms)</label>
                    <input className='release' type='range' min='0.01' max='12' step='0.01' onChange={this.updateRelease.bind(this)} value={this.state.release}/>
                </div>

            </div>
          </div>
        );
    }
}
export default Vco;



